.header {

    .image-container-top {
        width: 240px;
        height: 170px;
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-35px, 10px) rotate(100deg);
        background-image: url('../../images/yellow-flower.png');
        background-position: 100% 70%;
        background-size: 240px auto;

        @media (min-width: 900px) {
            width: 450px;
            height: 350px;
            background-position: 0% 70%;
            background-size: 470px auto;
            transform: translate(-30px,0) rotate(100deg);
        }
    }

    &__heading {
        font-size: 50px;
        margin: 180px 0 20px;

        @media (min-width: 600px) {
            font-size: 70px;
            margin: 150px 0 50px;
        }   
    }

    &__tagline {
        text-transform: uppercase;
        font-family: 'Montserrat', sans-serif;
        font-size: 14px;
        margin-bottom: 5px;

        @media (min-width: 600px) {
            margin-bottom: 10px;
            font-size: 20px;
        }
    }

    &__countdown {
        font-family: 'Montserrat', sans-serif;
        font-size: 14px;
        margin-bottom: 50px;

        @media (min-width: 600px) {
            font-size: 20px;
        }
    }

    .nav {
        &__list {
            list-style: none;
            display: flex;
            justify-content: space-around;
            align-items: center;
            padding-left: 0;
        }

        &__item {
                color: #B8D9A5;
                text-decoration: none;
                font-size: 20px;

                @media (min-width: 500px) {
                    font-size: 26px;
                }

                &:hover,
                &:focus {
                    text-decoration: underline;
                    cursor: pointer;
                }

            
        }
    }
}