.home {
    margin: 100px 0;

    &__hero {
        width: calc( 100% - 40px );
        height: auto;
    }

    &__date-container {
        display: flex;
        justify-content: center;
        align-items: center;

        &__date,
        &__location {
            font-size: 60px;
            width: 200px;
            padding: 0 100px;

            @media (max-width: 600px) {
                font-size: 30px;
                width: 100px;
                padding: 0 50px;
            }
        }

        &__date {
            border-right: 2px solid #B8D9A5;
        }
    }

    .flower-container-center {
        background-image: url('../../images/yellow-flower.png');
        height: 200px;
        width: 200px;
        margin: 0 auto;
        transform: translate(20px, -40px) rotate(-30deg);
        background-position: 100% 70%;
        background-size: 240px auto;
    }

    &__details {

        &__date {
            font-size: 26px;
            margin-bottom: 50px;

            @media (max-width: 600px) {
                font-size: 20px;
            }
        }

        &__detail {
            font-family: 'Montserrat', sans-serif;
            margin: 20px;
            letter-spacing: 2px;

            @media (max-width: 600px) {
                font-size: 16px;
            }
        }
    }

    &__schedule {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin: 50px auto 0;
        border-bottom: 1px solid #B8D9A5;

        @media (min-width:500px) {
            width: max-content;
        }

        &__time,
        &__event {
            font-size: 12px;
            font-family: 'Montserrat', sans-serif;
            text-align: left;
            text-transform: uppercase;
            border-top: 1px solid #B8D9A5;
            margin: 0;
            padding: 10px;

            @media (min-width: 500px) {
                font-size: 16px;
                padding: 20px;
            }

            @media (min-width: 900px) {
                font-size: 26px;
                padding: 30px 50px;
            }
        }

        &__time {
            @media (min-width: 800px) {
                letter-spacing: 5px;
            }
        }
    }
}