@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;background:#05065a;font-family:"Dancing Script",cursive;position:relative}.App .container{max-width:1200px;border:solid 1px #05065a;margin:0 auto;padding:0 20px;color:#b8d9a5}.App .container h2{font-size:30px;margin:0}@media(min-width: 600px){.App .container h2{font-size:60px}}
.header .image-container-top{width:240px;height:170px;position:absolute;top:0;left:0;-webkit-transform:translate(-35px, 10px) rotate(100deg);transform:translate(-35px, 10px) rotate(100deg);background-image:url(/my-wedding-website/static/media/yellow-flower.3ca8c182.png);background-position:100% 70%;background-size:240px auto}@media(min-width: 900px){.header .image-container-top{width:450px;height:350px;background-position:0% 70%;background-size:470px auto;-webkit-transform:translate(-30px, 0) rotate(100deg);transform:translate(-30px, 0) rotate(100deg)}}.header__heading{font-size:50px;margin:180px 0 20px}@media(min-width: 600px){.header__heading{font-size:70px;margin:150px 0 50px}}.header__tagline{text-transform:uppercase;font-family:"Montserrat",sans-serif;font-size:14px;margin-bottom:5px}@media(min-width: 600px){.header__tagline{margin-bottom:10px;font-size:20px}}.header__countdown{font-family:"Montserrat",sans-serif;font-size:14px;margin-bottom:50px}@media(min-width: 600px){.header__countdown{font-size:20px}}.header .nav__list{list-style:none;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;padding-left:0}.header .nav__item{color:#b8d9a5;text-decoration:none;font-size:20px}@media(min-width: 500px){.header .nav__item{font-size:26px}}.header .nav__item:hover,.header .nav__item:focus{text-decoration:underline;cursor:pointer}
.footer{padding-bottom:130px}.footer__heading{border-bottom:3px solid #b8d9a5;width:150px;display:inline-block;padding-bottom:10px;margin:10px}.footer__date{font-family:"Montserrat",sans-serif}.footer .image-container-bottom{width:240px;height:170px;position:absolute;bottom:0;right:0;-webkit-transform:translate(25px, 0px) rotate(290deg);transform:translate(25px, 0px) rotate(290deg);background-image:url(/my-wedding-website/static/media/yellow-flower.3ca8c182.png);background-position:100% 70%;background-size:240px auto}@media(min-width: 900px){.footer .image-container-bottom{width:450px;height:350px;background-position:0% 70%;background-size:470px auto;-webkit-transform:translate(0px, 20px) rotate(290deg);transform:translate(0px, 20px) rotate(290deg)}}
.home{margin:100px 0}.home__hero{width:calc( 100% - 40px );height:auto}.home__date-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.home__date-container__date,.home__date-container__location{font-size:60px;width:200px;padding:0 100px}@media(max-width: 600px){.home__date-container__date,.home__date-container__location{font-size:30px;width:100px;padding:0 50px}}.home__date-container__date{border-right:2px solid #b8d9a5}.home .flower-container-center{background-image:url(/my-wedding-website/static/media/yellow-flower.3ca8c182.png);height:200px;width:200px;margin:0 auto;-webkit-transform:translate(20px, -40px) rotate(-30deg);transform:translate(20px, -40px) rotate(-30deg);background-position:100% 70%;background-size:240px auto}.home__details__date{font-size:26px;margin-bottom:50px}@media(max-width: 600px){.home__details__date{font-size:20px}}.home__details__detail{font-family:"Montserrat",sans-serif;margin:20px;letter-spacing:2px}@media(max-width: 600px){.home__details__detail{font-size:16px}}.home__schedule{display:grid;grid-template-columns:1fr 1fr;margin:50px auto 0;border-bottom:1px solid #b8d9a5}@media(min-width: 500px){.home__schedule{width:-webkit-max-content;width:max-content}}.home__schedule__time,.home__schedule__event{font-size:12px;font-family:"Montserrat",sans-serif;text-align:left;text-transform:uppercase;border-top:1px solid #b8d9a5;margin:0;padding:10px}@media(min-width: 500px){.home__schedule__time,.home__schedule__event{font-size:16px;padding:20px}}@media(min-width: 900px){.home__schedule__time,.home__schedule__event{font-size:26px;padding:30px 50px}}@media(min-width: 800px){.home__schedule__time{letter-spacing:5px}}
.FAQ__heading{font-size:40px}
.question-answer{margin:50px auto;max-width:800px}.question-answer__question{font-size:36px;margin:20px;letter-spacing:2px}.question-answer__answer{font-family:"Montserrat",sans-serif;font-size:18px}
